import * as React from 'react'
import { connect } from '../../lib/ContextProvider'
import { IStores } from '../../stores'
import { Link } from 'react-router'
const { Button, Input } = require('rebass')

class LoginForm extends React.Component<any, any> {
	context: {
		stores: IStores
	}

	render () {
		let { authStore } = this.context.stores
		let { loginForm } = authStore
		let { email, password } = loginForm

		return (
			<form
				className='max-width-2 mx-auto'
				onSubmit={(e: Event) => {
					e.preventDefault()
					authStore.signin(email, password)
				}}
			>
				<Input
					name='Email'
					label='Email'
					onChange={(e: { target: HTMLInputElement }) => {
						authStore.changeLoginEmail(e.target.value)
					}}
					type='email'
					value={email}
				/>
				<Input
					name='Password'
					label='Password'
					onChange={(e: { target: HTMLInputElement }) => {
						authStore.changeLoginPassword(e.target.value)
					}}
					type='password'
					value={password}
				/>
				<Button
					theme='primary'
				>Submit</Button>
				<Link className='ml1' to='/signup'>
					<Button theme='secondary'>Signup</Button>
				</Link>
			</form>
		)
	}
}

export default connect(LoginForm)



