import React = require('react')
import { IStores } from '../../stores'
import { connect } from '../../lib/ContextProvider'
const { Button, Input } = require('rebass')

class SignupForm extends React.Component<any, any> {
	context: {
		stores: IStores
	}

	render () {
		let { authStore } = this.context.stores
		let { signupForm } = authStore
		let { email, password } = signupForm

		return (
			<form
				className='max-width-2 mx-auto'
				onSubmit={(e: Event) => {
					e.preventDefault()
					authStore.signup(email, password)
				}}
			>
				<Input
					label='Email'
					name='email'
					onChange={(e: { target: HTMLInputElement }) => {
						authStore.changeSignupEmail(e.target.value)
					}}
					type='email'
					value={email}
				/>
				<Input
					name='password'
					label='Password'
					type='password'
					onChange={(e: { target: HTMLInputElement }) => {
						authStore.changeSignupPassword(e.target.value)
					}}
					value={password}
				/>
				<Button theme='secondary'>Submit</Button>
			</form>
		)
	}
}

export default connect(SignupForm)



