import React = require('react')
import { observer } from 'mobx-react'
import { IStores } from '../stores'
import ReactRouter from 'react-router'

class ContextProvider extends React.Component<IContextProviderProps, any> {
	static childContextTypes = {
		context: React.PropTypes.object.isRequired
	}

	getChildContext () {
		return this.props.context
	}

	render () {
		return this.props.children
	}
}

interface IContextProviderProps {
	children?: JSX.Element
	context: {
		router?: ReactRouter.Router
		stores: IStores
	}
}

export function connect<P> (component: React.ComponentClass<P>): React.ComponentClass<P> {
	if (!component) {
		throw new Error('Not valid component passed to connect')
	}

	component.contextTypes = {
		router: React.PropTypes.object,
		stores: React.PropTypes.object
	}

	return observer(component)
}

export default ContextProvider



