import React = require('react')
import LoginForm from './LoginForm'
import { connect } from '../../lib/ContextProvider'
import { Container } from 'rebass'

@connect
class Login extends React.Component<any, any> {
	render () {
		return (
			<Container>
				<LoginForm />
			</Container>
		)
	}
}

export default Login



