import Root from '../client/views/Root'
import Dashboard from '../client/views/Dashboard'
import Login from '../client/views/Login'
import Profile from '../client/views/Profile'
import Signup from '../client/views/Signup'

export default {
	path: '/',
	component: Root,
	indexRoute: {
		component: Dashboard
	},
	childRoutes: [
		{
			component: Signup,
			path: '/signup'
		},
		{
			component: Login,
			path: '/login'
		},
		{
			component: Profile,
			path: '/profile'
		}
	]
}



